import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
// import { log } from 'node:console';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {
  cartItem : any =[]
  priceArray: any =[]
  total:any;
  constructor(private http : HttpClient) {
    this.cartItems();
   }

  ngOnInit(): void {
  }
  cartItems(){
    let apiUrl = 'https://apifromashu.herokuapp.com/api/cakecart';
    this.http.post(apiUrl , {}).subscribe((res: any) => {
      console.log('cart items' , res);
      this.cartItem = res.data;
      console.log(this.cartItem);
    },
    (error) => {
      console.log('this is response' + error);
    
    }
    );
    this.priceArray = this.cartItem.map((ele)=> ele.price)
    console.log(this.priceArray);
    
  }
  

  removeCake(i:any){
    console.log(this.cartItem);
    let apiUrl = 'https://apifromashu.herokuapp.com/api/removecakefromcart';
    this.http.post(apiUrl, {cakeid: i}).subscribe((response : any) => {
      console.log('cart items', response);
      this.cartItems();
      
    },
    (error) => {
      console.log('this is response ' , error);;
    
      
    })
  }
}
